.snip1268 {
 
  transition: all 0.6s ease;
  position: relative;
}
.snip1268 .image {
  position: relative;
}
.snip1268 img {
 
  transition: opacity 0.35s;
}
.snip1268 .icons 
  {
  position: absolute;
  left: 20px;
  right: 20px;
  opacity: 0;
}
.snip1268 .icons {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
  top: 20px;
  display: flex;
  justify-content: space-between;
}
.snip1268 .icons a {
  width: 32.5%;
}
.snip1268 .icons a:hover i {
  color: #ffffff;
  opacity: 1;
}
.snip1268 .icons i {
  font-size: 20px;
  color: #000000;
  opacity: 0.7;
}
 
.snip1268 figcaption {
  padding: 20px 20px 20px;
}
 
.snip1268 h2 {
  margin-bottom: 10px;
  font-weight: 700;
}
.snip1268 .price {
  font-size: 1.3em;
  opacity: 0.5;
  font-weight: 700;
  text-align: ce;
}
.snip1268:hover img,
.snip1268.hover img {
  opacity: 0.8;
}
.snip1268:hover .icons,
.snip1268.hover .icons
{
  transform: translateX(0);
  opacity: 1;
}
 
*:before, *:after {
  position: absolute;
}
.button {
  margin-top: 2%;
  color: #fff;
  border: 3px solid #bbb;
  border-radius: 50px;
  padding: 0.8rem 2rem;
  font: 24px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
}
.button:hover{
  background-color: #bbb;
}
.button-hand button:before {
  content: "ðŸ‘‡";
  font-size: 40px;
  transform: scaleX(-1);
  right: 0px;
  top: -50px;
  animation: up-down 1s infinite;
}
.button-hand button .hands:before, .button-hand button .hands:after {
  content: "ðŸ‘‡";
  font-size: 40px;
  opacity: 0;
  transition: 0.4s ease-in-out;
}
.button-hand button .hands:before {
  transform: rotate(-60deg);
  left: -45px;
  top: -10px;
}
.button-hand button .hands:after {
  transform: rotate(170deg);
  right: 30px;
  top: 50px;
}
.button-hand button:hover .hands:before {
  opacity: 1;
  left: -35px;
}
.button-hand button:hover .hands:after {
  opacity: 1;
  top: 40px;
}

@keyframes up-down {
  50% {
    margin-top: -20px;
  }
}