.button {
  margin-top: 2%;
  color: #fff;
  border: 3px solid #bbb;
  border-radius: 50px;
  padding: 0.8rem 2rem;
  font: 24px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
}
.button:hover{
  background-color: #bbb;
}
.button-hand button:before {
  content: "\1F447";
  font-size: 40px;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  right: 0px;
  top: -50px;
  -webkit-animation: up-down 1s infinite;
          animation: up-down 1s infinite;
}
.button-hand button .hands:before, .button-hand button .hands:after {
  content: "\1F447";
  font-size: 40px;
  opacity: 0;
  transition: 0.4s ease-in-out;
}
.button-hand button .hands:before {
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
  left: -45px;
  top: -10px;
}
.button-hand button .hands:after {
  -webkit-transform: rotate(170deg);
          transform: rotate(170deg);
  right: 30px;
  top: 50px;
}
.button-hand button:hover .hands:before {
  opacity: 1;
  left: -35px;
}
.button-hand button:hover .hands:after {
  opacity: 1;
  top: 40px;
}

